import React, { useEffect, useState } from "react";
import SubscribeSummary from "./Subscribe/SubscribeSummary";
import SubscribeItem from "./Subscribe/SubscribeItem";
import axios from "axios";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { Button } from "react-bootstrap";
import { FaShippingFast } from "react-icons/fa";
import { toast, ToastContainer } from "react-toastify";
import { Minus, Plus, Truck } from "lucide-react";
import { MdMyLocation } from "react-icons/md";
import { LoadScript, Autocomplete } from "@react-google-maps/api";

import Cookies from "js-cookie";
import CryptoJS from "crypto-js";
import moment from "moment";
const GOOGLE_API_KEY = process.env.REACT_APP_GOOGLE_API_KEY; // Replace with your Google Maps API key
const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
const encryptedToken = Cookies.get("token");
const token = encryptedToken
  ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
    CryptoJS.enc.Utf8
  )
  : null;
const encryptedUser = Cookies.get("user");
const user = encryptedUser
  ? JSON.parse(
    CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
      CryptoJS.enc.Utf8
    )
  )
  : null;

const TopupModal = ({ closeModal, setBalance, setTransactions }) => {
  const [amount, setAmount] = useState("");
  const params = useParams();
  let location = useLocation();
  const handleSubmit = async (event) => {
    event.preventDefault();
    const topupAmount = parseFloat(amount);

    if (!amount || topupAmount <= 0) {
      toast.error("Please enter a valid amount.");
      return;
    }

    if (topupAmount < 300) {
      toast.error("The minimum top-up amount is Rs. 300.");
      return;
    }

    try {
      const config = {
        url: "/user/topup",
        method: "post",
        baseURL: "https://majesticmall.net/api",
        hearder: { "content-type": "application/json" },
        headers: {
          Authorization: `Bearer ${token}`,
        },
        data: {
          userId: user?._id,
          amount: topupAmount,
        },
      };

      const response = await axios.post("https://majesticmall.net/api/user/addPaymentPhone",
        { userId: user?._id, username: user?.name, Mobile: user?.mobile, orderId: "Wallet" + Math.random(0, 10), amount: topupAmount, config: JSON.stringify(config), redirecturl: `/wallet`, callBack: `/` },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (response.status == 200) {
        return window.location.assign(response.data?.url?.url);
      }
    } catch (error) {
      // console.error("Error topping up the wallet:", error);
      if (error.response) {
        toast.error(error.response.data.error);
      } else
        toast.error("Error during top-up. Please try again.");
    } finally {
      closeModal();
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-6 w-96">
        <h2 className="text-xl font-bold mb-4">Topup Your Wallet</h2>
        <form onSubmit={handleSubmit}>
          <label htmlFor="amount" className="block mb-2">
            Enter Amount (Rs):
          </label>
          <input
            type="number"
            id="amount"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            min="1"
            required
            className="w-full p-2 border border-gray-300 rounded-md mb-4"
          />
          <p className="text-center text-sm text-white mb-4 bg-[#be343b] py-2">
            Minimum recharge amount is Rs. 300
          </p>
          <div className="flex justify-end gap-2">
            <button
              type="submit"
              className="bg-[#000] text-white px-4 py-2 rounded-md hover:bg-[#4a6c0a]"
            >
              Add Money
            </button>
            <button
              type="button"
              onClick={closeModal}
              className="bg-gray-200 text-gray-700 px-4 py-2 rounded-md hover:bg-gray-300"
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

const DaySquare = ({ day, dayQuantities, selectedDay, setSelectedDay }) => {
  return (
    <div
      className={`flex flex-col items-center p-2 rounded-lg cursor-pointer transition-colors
        ${selectedDay === day
          ? "bg-[#000]/20 border-2 border-[#000]"
          : "bg-gray-50 border border-gray-200 hover:bg-[#000]/10"
        }`}
      onClick={() => setSelectedDay(day)}
    >
      <div className="text-sm font-medium">{day}</div>
      <div className="text-lg font-bold text-[#000]">
        {dayQuantities[day]}
      </div>
    </div>
  );
};

const SubscribeCart = () => {
  const [shipToDifferentAddress, setShipToDifferentAddress] = useState(false);
  const [selectedAddress, setSelectedAddress] = useState("");
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
      CryptoJS.enc.Utf8
    )
    : null;
  const [isTopupModalOpen, setIsTopupModalOpen] = useState(false);
  const openTopupModal = () => setIsTopupModalOpen(true);
  const closeTopupModal = () => setIsTopupModalOpen(false);
  const [newAddress, setNewAddress] = useState({
    fullName: "",
    street: "",
    city: "",
    state: "",
    country: "",
    pincode: "",
    number: "",
    anumber: "",
    email: "",
  });

  const handleNewAddressChange = (e) => {
    const { name, value } = e.target;
    setNewAddress({ ...newAddress, [name]: value });
    if (name === "fullName") {
      if (!/^[a-zA-Z]*$/.test(value)) {
        // console.log("Invalid name: Only alphabets are allowed");
        return;
      }
      if (value.length < 3) {
        // console.log("Invalid name: Minimum 3 characters");
        return;
      }
    }
    if (name === "city") {
      if (!/^[a-zA-Z]*$/.test(value)) {
        // console.log("Invalid city: Only alphabets are allowed");
        return;
      }
      if (value.length < 3) {
        // console.log("Invalid city: Minimum 3 characters");
        return;
      }
    }
    if (name === "street" && value.length < 3) {
      // console.log("Invalid street: Minimum 3 characters");
      return;
    }
    if (name === "state") {
      if (!/^[a-zA-Z]*$/.test(value)) {
        // console.log("Invalid state: Only alphabets are allowed");
        return;
      }
      if (value.length < 3) {
        // console.log("Invalid state: Minimum 3 characters");
        return;
      }
    }
    if (name === "country") {
      if (!/^[a-zA-Z]*$/.test(value)) {
        // console.log("Invalid country: Only alphabets are allowed");
        return;
      }
      if (value.length < 3) {
        // console.log("Invalid country: Minimum 3 characters");
        return;
      }
    }
    if (name === "pincode" && (!/^\d*$/.test(value) || value.length > 6)) {
      // console.log("Invalid pincode: Must be a number with max 6 digits");
      return;
    }
    if (name === "number" && (!/^\d*$/.test(value) || value.length > 10)) {
      // console.log("Invalid mobile: Must be a number with max 10 digits");
      return;
    }
    if (name === "anumber" && (!/^\d*$/.test(value) || value.length > 10)) {
      // console.log("Invalid mobile: Must be a number with max 10 digits");
      return;
    }
  };
  const [paymentMethod, setPaymentMethod] = useState("");
  // const encryptedUser = Cookies.get('user');
  //  const user = encryptedUser ? JSON.parse(CryptoJS.AES.decrypt(encryptedUser , encryptionKey).toString(CryptoJS.enc.Utf8)):null ;
  const encryptedUser = Cookies.get("user");
  // const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const user = encryptedUser
    ? JSON.parse(
      CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    )
    : null;
  const [name, setname] = useState("");
  const [isModalVisible, setIsModalVisible] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };
  const [mobile, setmobile] = useState("");
  const [pincode, setpincode] = useState("");
  const [Email, setEmail] = useState("");
  const [Area, setArea] = useState("");

  const [city, setcity] = useState("");
  const [state, setstate] = useState("");
  const [country, setcountry] = useState("");
  const addAddress = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post(
        "https://majesticmall.net/api/user/addaddress",
        {
          userId: user._id,
          ...newAddress,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        toast.info("Address added successfully");
        getAddress();
        setNewAddress({
          fullName: "",
          street: "",
          city: "",
          state: "",
          country: "",
          pincode: "",
          number: "",
          anumber: "",
          email: "",
        });
      }
    } catch (error) {
      toast.info(error.response?.data?.error || "Error adding address");
    }
  };
  const openship = () => {
    setShipToDifferentAddress(!shipToDifferentAddress);
    setSelectedAddress("");
  };

  let deleteAddress = async (id) => {
    try {
      let res = await axios.delete(
        "https://majesticmall.net/api/user/deleteaddress/" + id,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        toast.info("Successfully deleted");
        window.location.reload();
      }
    } catch (error) {
      console.log(error);
    }
  };
  const [address, setaddress] = useState([]);

  let getAddress = async () => {
    try {
      const userId = user?._id;
      if (!userId) {
        console.error("User ID is undefined");
        return;
      }

      const res = await axios.get(
        `https://majesticmall.net/api/user/address/${userId}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (res.status === 200) {
        setaddress(res.data.success);
        // console.log(res.data.success);
      }
    } catch (error) {
      console.error("Error fetching address:", error);
    }
  };
  useEffect(() => {
    getAddress();
  }, []);
  const [product, setProduct] = useState([]);

  const [balance, setBalance] = useState(0);
  const [transactions, setTransactions] = useState([]);
  let location = useLocation();
  const { selectedWeight } = location?.state;
  // console.log(selectedWeight);
  const params = useParams();

  const getProducts = async () => {
    try {
      const res = await axios.get(
        "https://majesticmall.net/api/admin/Getproductlist",
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setProduct(
          res.data.ProductList.filter((item) => item.status === "Active")
        );
      } else {
        console.error("Failed to fetch products");
      }
    } catch (error) {
      console.error("Error fetching products:", error);
    }
  };

  useEffect(() => {
    getProducts();
  }, []);

  const item = product?.find((items) => items?._id === params.id);
  const [frequency, setFrequency] = useState("Everyday");
  const [isDateFilterApplied, setIsDateFilterApplied] = useState(false);
  const [quantity, setQuantity] = useState(1);
  const [nthInterval, setNthInterval] = useState("");
  const [nthIntervalActive, setNthIntervalActive] = useState(false);
  const [selectedOrders, setSelectedOrders] = useState(null);
  // const navigate = useNavigate();

  const today = new Date();
  const tomorrow = new Date(today);
  tomorrow.setDate(today.getDate() + 1);
  const minDate = tomorrow.toISOString().split("T")[0];

  const [startDate, setStartDate] = useState(moment(tomorrow).format("YYYY-MM-DD"));
  const [endDate, setEndDate] = useState(moment(tomorrow).format("YYYY-MM-DD"));
  // const handleShipToDifferentAddressChange = () => {
  //   setShipToDifferentAddress(!shipToDifferentAddress);
  // };

  const handleAddressChange = (e) => {
    const selectedValue = e.target.value;
    setSelectedAddress(JSON.parse(selectedValue));
    setShipToDifferentAddress(false);
  };

  const pricePerItem = selectedWeight?.price;

  const [productValue, setProductValue] = useState(pricePerItem * quantity);

  const [discount, setDiscount] = useState(
    user.club ? item?.clubdiscount : item?.discount
  );
  const [deliveryCharges, setDeliveryCharges] = useState(0);

  const [subtotal, setSubtotal] = useState(productValue - discount);
  const [total, setTotal] = useState(subtotal + deliveryCharges);

  const [dayQuantities, setDayQuantities] = useState({
    Sunday: 0,
    Monday: 0,
    Tuesday: 0,
    Wednesday: 0,
    Thursday: 0,
    Friday: 0,
    Saturday: 0,
  });

  const [selectedDay, setSelectedDay] = useState("");

  const [totalOrders, setTotalOrders] = useState(0);
  const [totalQuantity, setTotalQuantity] = useState(0);

  useEffect(() => {
    if (startDate && endDate) {
      const start = new Date(startDate);
      const end = new Date(endDate);
      const daysDiff = Math.ceil((end - start) / (1000 * 60 * 60 * 24)) + 1;

      let orders = 0;
      let qty = 0;

      if (frequency === "Everyday") {
        orders = daysDiff;
        qty = daysDiff * quantity;
      } else if (frequency === "On Interval" && nthInterval && startDate !== endDate) {
        orders = Math.floor(daysDiff / nthInterval);
        qty = orders * quantity;
      } else if (startDate === endDate && frequency === "On Interval" && nthInterval) {
        orders = daysDiff;
        qty = daysDiff * quantity;
      } else if (frequency === "Customize") {
        orders = 0;
        qty = 0;
        for (let d = new Date(start); d <= end; d.setDate(d.getDate() + 1)) {
          const day = d.toLocaleString("en-us", { weekday: "long" });
          if (dayQuantities[day] > 0) {
            orders++;
            qty += dayQuantities[day];
          }
        }
      }

      setTotalOrders(orders);
      setTotalQuantity(qty);
      setIsDateFilterApplied(true);
      setSelectedOrders(null);
    }
    // else if (selectedOrders) {
    //   setTotalOrders(selectedOrders);
    //   setTotalQuantity(
    //     frequency === "Customize"
    //       ? Object.values(dayQuantities).reduce((a, b) => a + b, 0) *
    //           selectedOrders
    //       : selectedOrders * quantity
    //   );
    //   setIsDateFilterApplied(false);
    // }
  }, [
    startDate,
    endDate,
    frequency,
    nthInterval,
    dayQuantities,
    selectedOrders,
    quantity,
  ]);
  useEffect(() => {
    if (user && !selectedAddress) {
      setSelectedAddress(user);
    }
    fetchWalletData();
  }, []);

  const fetchWalletData = async () => {
    try {
      const response = await axios.get(
        `https://majesticmall.net/api/user/getwallet/${user._id}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      setBalance(response.data.success.balance);

      setTransactions(response.data.success.transactions);
    } catch (error) {
      console.error("Error fetching wallet data:", error);
    }
  };

  const roundToTwoDecimals = (num) =>
    Math.round((num + Number.EPSILON) * 100) / 100;
  useEffect(() => {
    const itemPrice = pricePerItem;
    const newProductValue = roundToTwoDecimals(itemPrice * totalQuantity);
    setProductValue(newProductValue);

    const discountPercentage = user.club
      ? item?.clubdiscount / 100
      : item?.discount / 100;
    const newDiscount = roundToTwoDecimals(
      newProductValue * discountPercentage
    );
    setDiscount(newDiscount);

    const newSubtotal = roundToTwoDecimals(newProductValue - newDiscount);
    setSubtotal(newSubtotal);

    const newTotal = roundToTwoDecimals(newSubtotal + deliveryCharges);
    setTotal(newTotal);
  }, [totalQuantity, pricePerItem, item, deliveryCharges, balance]);

  const handleFrequencyChange = (newFrequency) => {
    setFrequency(newFrequency);
    setNthIntervalActive(false);
    setSelectedOrders(null);

    if (newFrequency === "Everyday" || newFrequency === "On Interval") {
      setQuantity(1);
    } else if (newFrequency === "Customize") {
      setQuantity(1);
    }
  };

  const handleNthIntervalChange = (e) => {
    let targetDate = new Date(endDate);
    const today = new Date();
    const diffInDays = Math.ceil((targetDate - today) / (1000 * 60 * 60 * 24));

    if (diffInDays < Number(e.target.value)) {
      targetDate.setDate(today.getDate() + Number(e.target.value));
    }
    // setEndDate(moment(targetDate).format("YYYY-MM-DD"));
    setNthInterval(Number(e.target.value));
  };

  const handleNthDayClick = () => {
    setNthIntervalActive(true);
  };

  const handleOrdersSelection = (orders) => {
    setSelectedOrders(orders);
  };

  const handleDayQuantityChange = (day, amount) => {
    setDayQuantities((prevQuantities) => ({
      ...prevQuantities,
      [day]: Math.max(0, prevQuantities[day] + amount),
    }));
  };
  const getNextOccurrence2 = (dayName, quantity) => {
    const daysOfWeek = [
      "Sunday",
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday",
    ];
    const today = new Date();
    const todayIndex = today.getDay();
    const targetIndex = daysOfWeek.indexOf(dayName);

    let daysToAdd = targetIndex - todayIndex;
    if (daysToAdd < 0) daysToAdd += 7; // Ensure the next occurrence is in the future

    const nextDate = new Date();
    nextDate.setDate(today.getDate() + daysToAdd + (quantity - 1) * 7); // Add (quantity - 1) weeks

    return nextDate;
  };

  const handleQuantityChange = (amount) => {
    if (frequency === "Customize" && selectedDay) {
      setDayQuantities((prevQuantities) => {
        const newQuantities = {
          ...prevQuantities,
          [selectedDay]: Math.max(
            0,
            (prevQuantities[selectedDay] || 0) + amount
          ),
        };

        // Recalculate endDate based on the updated quantity
        const nextDate = getNextOccurrence2(
          selectedDay,
          newQuantities[selectedDay]
        );
        // setEndDate(moment(nextDate).format("YYYY-MM-DD"));

        return newQuantities;
      });
    } else {
      setQuantity((prevQuantity) => {
        const newQuantity = Math.max(1, prevQuantity + amount);

        // Recalculate endDate for non-customized frequency
        const nextDate = new Date();
        nextDate.setDate(new Date().getDate() + newQuantity);
        // setEndDate(moment(nextDate).format("YYYY-MM-DD"));

        return newQuantity;
      });
    }
  };

  const saveSubscription = async () => {
    if (balance === 0) {
      toast.info("Please recharge your wallet.");
      openTopupModal();
      return;
    }
    if (balance < productValue) {
      toast.info("Insufficient wallet balance. Please recharge your wallet.");
      openTopupModal();
      return;
    }

    if (!selectedAddress) {
      toast.info("Please select a delivery address.");
      return;
    }
    if (!startDate) {
      toast.info("Please select start date.");
      return;
    }
    // if (
    //   (frequency === "Customize" || frequency === "On Interval") &&
    //   !endDate &&
    //   selectedOrders === null
    // ) {
    //   toast.info("Please select an end date.");
    //   return;
    // }

    try {
      const response = await axios.post(
        "https://majesticmall.net/api/user/create",
        {
          userId: user?._id,
          productId: item?._id,
          frequency,
          quantity,
          startDate,
          endDate: startDate == endDate ? null : endDate,
          totalOrders,
          totalQuantity,
          productValue,
          discount,
          subtotal,
          deliveryCharges,
          total,
          shippingAddress: selectedAddress,
          dayQuantities,
          nthInterval: frequency === "On Interval" ? nthInterval : undefined,
          selectedWeight: {
            value: selectedWeight?.value,
            units: selectedWeight?.units,
            price: selectedWeight?.price,
          },
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (response.status === 201) {
        toast.info("Subscription added successfully.");
        setTimeout(() => {
          window.location.assign("/");
        }, 300)

      }
      // if (response.status === 201) {
      //
      //   const debitResponse = await axios.post(
      //     "https://majesticmall.net/api/user/debit",
      //     {
      //       userId: user?._id,
      //       amount: subtotal,
      //       detail: `Subscription for product ${item?.productname}`,
      //     }
      //   );

      //   if (debitResponse.status === 200) {
      //
      //     const creditAmount = calculateWalletCredit(subtotal);

      //
      //     const creditResponse = await axios.post(
      //       "https://majesticmall.net/api/user/credit",
      //       {
      //         userId: user?._id,
      //         amount: creditAmount,
      //         detail: `Credit for order value ${subtotal}`,
      //       }
      //     );

      //     if (creditResponse.status === 200) {
      //
      //toast.info("Subscription created and wallet updated successfully."
      // );
      //       window.location.assign("/");
      //     } else {

      // toast.info("Subscription created and amount debited, but failed to credit wallet."
      // );
      //     }
      //   } else {

      //  toast.info("Subscription created, but failed to deduct amount from wallet."
      // );
      //   }
      // }
    } catch (error) {
      console.error("Error in subscription process:", error);
      toast.info(
        "Failed to complete the subscription process. Please try again."
      );
    }
  };

  const getOrdinal = (n) => {
    if (n === 1) return "st";
    if (n === 2) return "nd";
    if (n === 3) return "rd";
    return "th";
  };

  const [autocomplete, setAutocomplete] = useState(null);
  const handlePlaceSelect = () => {
    if (!autocomplete) return;

    const place = autocomplete.getPlace();
    if (!place.geometry || !place.address_components) return;

    const addressComponents = place.address_components;

    let street = place?.name;
    let city = "";
    let state = "";
    let country = "";
    let pincode = "";
    let lat = place.geometry.location.lat();
    let lng = place.geometry.location.lng();

    // Extract address components
    addressComponents.forEach((component) => {
      const types = component.types;

      if (types.includes("locality")) {
        city = component.long_name;
      }
      if (types.includes("administrative_area_level_1")) {
        state = component.short_name;
      }
      if (types.includes("country")) {
        country = component.long_name;
      }
      if (types.includes("postal_code")) {
        pincode = component.long_name;
      }
    });

    // Update state with extracted data
    setNewAddress({
      ...newAddress,
      street,
      city,
      state,
      country,
      pincode,
      lat,
      lng,
    });
  };

  const getAddressFromCoordinates = (lat, lng) => {
    fetch(
      `https://maps.googleapis.com/maps/api/geocode/json?latlng=${lat},${lng}&key=${GOOGLE_API_KEY}`
    )
      .then((response) => response.json())
      .then((data) => {
        if (data.status === "OK" && data.results.length > 0) {
          const place = data.results[0];
          const components = place.address_components;

          let street = place?.formatted_address || "";
          let city = "";
          let state = "";
          let country = "";
          let pincode = "";

          components.forEach((component) => {
            const types = component.types;
            if (types.includes("locality")) city = component.long_name;
            if (types.includes("administrative_area_level_1"))
              state = component.long_name;
            if (types.includes("country")) country = component.long_name;
            if (types.includes("postal_code")) pincode = component.long_name;
          });

          setNewAddress({
            ...newAddress,
            street,
            city,
            state,
            country,
            pincode,
            lat,
            lng,
          });
        } else {
          toast.info("Unable to fetch address details.");
        }
      })
      .catch(() => toast.info("Failed to fetch address"));
  };

  const getLocation = () => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          getAddressFromCoordinates(latitude, longitude);
        },
        (err) => toast.info(err.message)
      );
    } else {
      toast.info("Geolocation is not supported by this browser.");
    }
  };

  const [distance, setDistance] = useState(0);

  useEffect(() => {
    const fetchDistance = async () => {
      if (!selectedAddress) return;
      if (!selectedAddress?.lat || !selectedAddress.lng)
        return toast.error("Your address not able to find please update");

      if (!window.google) return;

      const origin = { lat: 12.940025819400102, lng: 77.5477817106305 };
      const destination = {
        lat: selectedAddress?.lat,
        lng: selectedAddress.lng,
      };

      const service = new window.google.maps.DistanceMatrixService();

      service.getDistanceMatrix(
        {
          origins: [origin],
          destinations: [destination],
          travelMode: window.google.maps.TravelMode.DRIVING,
        },
        (response, status) => {
          if (status === "OK") {
            const result = response.rows[0].elements[0];
            setDistance(result.distance.text);
          } else {
            console.error("Distance Matrix request failed:", status);
          }
        }
      );
    };

    fetchDistance();
  }, [selectedAddress]);

  function check(distance) {
    if (!distance) return false;

    // Extract numeric value from the distance string
    let numericDistance = parseFloat(distance.match(/[\d.]+/)[0]);

    // Determine unit (meters or kilometers)
    let unit = distance.includes('km') ? 'km' : 'm';

    // Convert meters to kilometers if necessary
    if (unit === 'm') {
      numericDistance /= 1000; // Convert meters to kilometers
    }

    // Check if the distance is less than or equal to 2 kilometers
    if (numericDistance <= 2) {
      return true;
    } else {
      return false;
    }
  }



  // console.log("distance",check(distance));
  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      <ToastContainer />
      <h2 className="text-2xl font-bold text-gray-900 mb-8">
        Your Subscribe Items
      </h2>

      <div className="flex flex-col lg:flex-row gap-8">
        <div className="flex-grow">
          <div className="bg-white rounded-lg shadow-md p-4 mb-8">
            <div className="flex flex-col md:flex-row gap-6">
              <div className="w-full md:w-48 h-48 relative">
                <img
                  src={`https://majesticmall.net/product/${item?.Photos[0].file}`}
                  alt={item?.productname}
                  className="w-full h-full object-contain rounded-lg"
                  loading="lazy"
                />
              </div>
              <div className="flex-grow">
                <h3 className="text-xl font-bold text-[#000] mb-2">
                  {item?.productname}
                </h3>
                <h3 className="text-xl font-bold text-[#000] mb-4">
                  {selectedWeight?.value + " " + selectedWeight.units} - 
                  {selectedWeight.price}
                </h3>

                <div className="mb-6">
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Frequency:
                  </label>
                  <div className="flex flex-wrap gap-2">
                    {["Everyday", "On Interval"].map((freq) => (
                      <button
                        key={freq}
                        onClick={() => handleFrequencyChange(freq)}
                        className={`px-4 py-2 rounded-full text-sm font-medium transition-colors
                          ${frequency === freq
                            ? "bg-[#000] text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                          }`}
                      >
                        {freq}
                      </button>
                    ))}
                  </div>
                </div>
                {frequency === "On Interval" && (
                  <div className="mb-6">
                    <div className="flex flex-wrap gap-2">
                      {[2, 3, 4].map((n) => (
                        <button
                          key={n}
                          onClick={() => {
                            let targetDate = new Date(endDate);
                            const today = new Date();
                            const diffInDays = Math.ceil(
                              (targetDate - today) / (1000 * 60 * 60 * 24)
                            );

                            if (diffInDays < n) {
                              targetDate.setDate(today.getDate() + n);
                            }

                            // setEndDate(moment(targetDate).format("YYYY-MM-DD"));
                            setNthInterval(n);
                          }}
                          className={`px-4 py-2 rounded-lg text-sm font-medium
                            ${nthInterval === n
                              ? "bg-[#000]/20 text-[#000] border-2 border-[#000]"
                              : "bg-gray-50 text-gray-700 border border-gray-200 hover:bg-gray-100"
                            }`}
                        >
                          Every {n}
                          {getOrdinal(n)} Day
                        </button>
                      ))}
                      <button
                        onClick={handleNthDayClick}
                        className="px-4 py-2 rounded-lg text-sm font-medium bg-gray-50 text-gray-700 border border-gray-200 hover:bg-gray-100"
                      >
                        Custom Interval
                      </button>
                    </div>
                    {nthIntervalActive && (
                      <div className="mt-4">
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          Enter interval (days):
                        </label>
                        <input
                          type="number"
                          value={nthInterval}
                          onChange={handleNthIntervalChange}
                          className="w-32 p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#000]"
                          min="1"
                        />
                      </div>
                    )}
                  </div>
                )}
                {frequency === "Customize" && (
                  <div className="grid grid-cols-2 sm:grid-cols-4 md:grid-cols-7 gap-2 mb-6">
                    {[
                      "Sunday",
                      "Monday",
                      "Tuesday",
                      "Wednesday",
                      "Thursday",
                      "Friday",
                      "Saturday",
                    ].map((day) => (
                      <DaySquare
                        key={day}
                        day={day}
                        dayQuantities={dayQuantities}
                        selectedDay={selectedDay}
                        // setSelectedDay={setSelectedDay}
                        setSelectedDay={(day) => {
                          // const nextDate = getNextOccurrence(day);
                          // setEndDate(moment(nextDate).format("YYYY-MM-DD"));
                          setSelectedDay(day);
                        }}
                      />
                    ))}
                  </div>
                )}

                {item?.stock - item?.sellproduct <= 0 ? (
                  <p
                    style={{
                      color: "red",
                      paddingTop: "27px",
                      textAlign: "center",
                      fontWeight: "500",
                    }}
                  >
                    Out of Stock
                  </p>
                ) : (
                  <div className="flex items-center gap-4 mb-6">
                    <button
                      onClick={() => handleQuantityChange(-1)}
                      className="p-2 rounded-full bg-gray-100 hover:bg-gray-200"
                    >
                      <Minus size={20} />
                    </button>
                    <span className="text-xl font-bold">
                      {frequency === "Customize"
                        ? selectedDay
                          ? dayQuantities[selectedDay]
                          : 0
                        : quantity}
                    </span>
                    <button
                      onClick={() => handleQuantityChange(1)}
                      className="p-2 rounded-full bg-gray-100 hover:bg-gray-200"
                    >
                      <Plus size={20} />
                    </button>
                  </div>
                )}
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      Start Date
                    </label>
                    <input
                      type="date"
                      value={startDate}
                      onChange={(e) => setStartDate(e.target.value)}
                      min={minDate}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#000]"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-1">
                      End Date (Optional)
                    </label>
                    <input
                      type="date"
                      value={endDate == startDate ? "" : endDate}
                      onChange={(e) => setEndDate(e.target.value)}
                      min={minDate}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#000]"
                    />
                  </div>
                </div>
                {frequency !== "Customize" && !isDateFilterApplied && (
                  <div className="mb-6">
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Quick Order Selection
                    </label>
                    <div className="grid grid-cols-2 sm:grid-cols-4 gap-2">
                      {[7, 30, 60, 90].map((orderCount) => (
                        <button
                          key={orderCount}
                          onClick={() => handleOrdersSelection(orderCount)}
                          className={`py-3 px-4 rounded-lg text-sm font-medium transition-colors
                            ${selectedOrders === orderCount
                              ? "bg-[#000] text-white"
                              : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                            }`}
                        >
                          {orderCount} Orders
                        </button>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
          <div className="bg-white rounded-lg shadow-md p-6">
            <h3 className="text-xl font-bold flex items-center gap-2 mb-6">
              <Truck className="text-[#000]" />
              Delivery Address
            </h3>
            <select
              className="w-full p-2 border border-gray-300 rounded-md mb-4 focus:outline-none focus:ring-2 focus:ring-[#000]"
              value={JSON.stringify(selectedAddress)}
              onChange={handleAddressChange}
            >
              <option value="">Select an address</option>

              <option key={user._id} value={JSON.stringify(user)}>
                {user.name}, {user.address1}, {user.address2}
              </option>
              {address.map((address) => (
                <option key={address._id} value={JSON.stringify(address)}>
                  {address.fullName}, {address.street}, {address.city}
                </option>
              ))}
            </select>

            {selectedAddress && (
              <div className="bg-gray-100 p-4 rounded-md mb-4">
                <h3 className="font-semibold mb-2 text-gray-700">
                  Selected Address
                </h3>
                <p>
                  {selectedAddress.name
                    ? selectedAddress.name
                    : selectedAddress.fullName}
                </p>
                <p>
                  {selectedAddress.address1
                    ? selectedAddress.address1
                    : selectedAddress.street}
                </p>
                <p>
                  {selectedAddress.address2
                    ? selectedAddress.address2
                    : selectedAddress.city}
                  , {selectedAddress.state} {selectedAddress.pincode}
                </p>
                <p>{selectedAddress.country}</p>
                <p>
                  Phone:{" "}
                  {selectedAddress.mobile
                    ? selectedAddress.mobile
                    : selectedAddress.number}
                </p>
                <p>
                  Alternative Phone (Whatsapp number):{" "}
                  {selectedAddress.Amobile
                    ? selectedAddress.Amobile
                    : selectedAddress.anumber}
                </p>
                <p>Email: {selectedAddress.email}</p>
              </div>
            )}

            <div className="mb-4">
              <label className="flex items-center text-gray-700">
                <input
                  type="checkbox"
                  checked={shipToDifferentAddress}
                  onChange={() => openship()}
                  className="mr-2 form-checkbox h-5 w-5 text-[#000]"
                />
                Ship to a different address?
              </label>
            </div>

            {shipToDifferentAddress && (
              <form onSubmit={addAddress} className="space-y-4">
                <input
                  type="text"
                  name="fullName"
                  value={newAddress.fullName}
                  onChange={handleNewAddressChange}
                  placeholder="Full Name"
                  className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                  required
                />
                <div className="flex items-center relative  align-center  rounded-full">
                  <MdMyLocation
                    className="ml-2  cursor-pointer absolute left-3"
                    color="#000"
                    title="Current Location"
                    onClick={() => getLocation()}
                  />
                  <Autocomplete
                    onLoad={setAutocomplete}
                    onPlaceChanged={handlePlaceSelect}
                    className="w-100 px-2 "
                  >
                    <input
                      type="text"
                      name="street"
                      value={newAddress.street}
                      onChange={handleNewAddressChange}
                      placeholder="Street Address"
                      className="w-full    border border-gray-300  rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                      required
                      style={{ paddingLeft: "40px" }}
                    />
                  </Autocomplete>
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <input
                    type="text"
                    name="city"
                    value={newAddress.city}
                    onChange={handleNewAddressChange}
                    placeholder="City"
                    className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                    required
                  />
                  <input
                    type="text"
                    name="state"
                    value={newAddress.state}
                    onChange={handleNewAddressChange}
                    placeholder="State"
                    className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                    required
                  />
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <input
                    type="text"
                    name="country"
                    value={newAddress.country}
                    onChange={handleNewAddressChange}
                    placeholder="Country"
                    className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                    required
                  />
                  <input
                    type="text"
                    name="pincode"
                    value={newAddress.pincode}
                    onChange={handleNewAddressChange}
                    placeholder="Pincode"
                    className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                    required
                  />
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <input
                    type="tel"
                    name="number"
                    value={newAddress.number}
                    onChange={handleNewAddressChange}
                    placeholder="Phone Number"
                    className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                    required
                  />
                  <input
                    type="tel"
                    name="anumber"
                    value={newAddress.anumber}
                    onChange={handleNewAddressChange}
                    placeholder="Alternative Number (Whatsapp number)"
                    className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                    required
                  />
                </div>
                <input
                  type="email"
                  name="email"
                  value={newAddress.email}
                  onChange={handleNewAddressChange}
                  placeholder="Email"
                  className="w-full p-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-[#000]"
                  required
                />
                <button
                  type="submit"
                  className="w-full bg-[#000] text-white p-2 rounded-md hover:bg-[#000] transition duration-300"
                >
                  Add New Address
                </button>
              </form>
            )}
          </div>
        </div>
        <div className="lg:w-96">
          <div className="bg-white rounded-lg shadow-md p-6 sticky top-4">
            <h3 className="text-xl font-bold mb-6">Subscribe Summary</h3>

            <div className="space-y-4 mb-6">
              <div className="flex justify-between items-center">
                <span className="text-gray-600">Total Orders</span>
                <span className="font-bold">{totalOrders}</span>
              </div>
              <div className="flex justify-between items-center">
                <span className="text-gray-600">Total Quantity</span>
                <span className="font-bold">{totalQuantity}</span>
              </div>
              <div className="flex justify-between items-center">
                <span className="text-gray-600">Product Value</span>
                <span className="font-bold">{productValue?.toFixed(2)}</span>
              </div>
              <div className="flex justify-between items-center text-[#000]">
                <span>Wallet Balance</span>
                <span className="font-bold">{balance?.toFixed(2)}</span>
              </div>
            </div>
            {balance < productValue && (
              <button
                onClick={openTopupModal}
                className="w-full bg-[#be343b] text-white py-2 px-4 rounded-lg font-medium hover:bg-[#a02d33] transition-colors mb-2"
              >
                Recharge Wallet
              </button>
            )}
            <p className="text-center text-sm text-white mt-2 gradient1 rounded-md py-2 p-2">Our services are available within a 2 km radius.</p>
            <button
              onClick={saveSubscription}
              disabled={!check(distance)}
              className={`w-full bg-[#000] text-white py-3 px-4 rounded-lg font-medium hover:bg-[#000] transition-colors ${check(distance) ? "" : 'opacity-50'}`}
            >
              {check(distance) ? "Subscribe Now" : `Distance Exceeded your distance limit is ${distance}`}

            </button>
          </div>
        </div>
      </div>
      {isTopupModalOpen && (
        <TopupModal
          closeModal={closeTopupModal}
          setBalance={setBalance}
          setTransactions={setTransactions}
        />
      )}
    </div>
  );
};

export default SubscribeCart;
